<template>
  <div>
    <div class="m-container">
      <view-header title="系统首页"></view-header>
      <div class="m-table-div">
        <table>
          <thead>
            <th>序号</th>
            <th>登录时间</th>
            <th>IP</th>
            <th>地区</th>
            <th>浏览器</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in titles" :key="index">
              <td>{{index + 1}}</td>
              <td>2017-07-03 14:36:21</td>
              <td>183.13.135.1</td>
              <td>上海市</td>
              <td>Firefox45</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
export default {
  components: {
    ViewHeader
  },
  data () {
    return {
      titles: ['今日新增', '昨日新增', '本周新增', '上周新增', '本月新增', '上月新增', '系统总数']
    }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
</style>


